@import "../assets/styles/main.scss";

.helloContainer {
  // padding: 0px 100px 0px 100px;

.hello {
  height: 630px;
  background-color: white;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  font-family: $header-font;

  .hello-description {
    width: 100%;
    text-align: left;
    font-size: 36px;
    line-height: 1.5;
    margin-left: 80px;
  }
  .profile {
    width: 300px;
  }

  #workLink {
    font-size: 14px;
  }
  .linkHighlight {
    // color: white;
    background-color: $highlight-color;
    color: white;
    text-decoration: none;
    font-family: $header-font;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    padding-left: 3px;
  }
}
}

